<template>
    <div class="ui-container">
        <div class="amimate-item">
            <div class="am-content" style="overflow: auto;">
                <ul class="music-list">
                    <!-- <div class="amimate-close"><a >返回</a></div> -->
                    <li v-for="(item, index) in musicListData" class="music-item" @click="toPlayer(index)" :key="index">
                        <div class="l-img" :style="getImg(item.albummid)"></div>
                        <div class="l-s-img" :style="getImg(item.albummid)"></div>
                        <div class="r-text">
                            <h3>{{item.songname}}</h3>
                            <p>{{item.singername}}</p>
                            <p>{{item.albumname}}</p>
                            <div><i></i><i></i><i></i><i></i><i></i></div>
                            <a class="play-btn">播放</a>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    mounted () {
    },
    computed: {
        musicListData () {
            return this.$store.state.list
        }
    },
    methods: {
        getImg (id) {
            return {
                backgroundImage: `url(https://y.gtimg.cn/music/photo_new/T002R300x300M000${id}.jpg)`
            }
        },
        toLister () {
            this.transitionName = 'rotateCubeInRight'
            this.current = 'list'
        },
        toPlayer (i) {
            this.$router.push({path: '/player', query: {id: i}})
            document.querySelector('#h5audio_media').pause()
        }
    }
}
</script>

<style lang="scss">
@import '../assets/style/global.scss';
.tabs {
    display: flex;
    a {
        flex: 1;
        text-align: center;
        line-height: 50px;
        &.on {
            color: #f00;
        }
    }
}
.mu-player {
    overflow: hidden;
    img.ms {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 3;
        left: 50%;
        height: 100%;
        object-fit: cover;
        transform: scale(1.1) translateZ(0) translateX(-50%);
        filter: blur(36px);
    }
    img.image-cd {
        display: block;
        margin: auto;
        width: 250px;
        height: 250px;
        border-radius: 50%;
        position: relative;
        z-index: 100;
    }
}
.music-list {
    padding-top: 20px;
    padding-bottom: 20px;
    .music-item {
        margin-bottom: 20px;
        padding: 0 20px;
        display: flex;
        align-items: center;
        position: relative;
        .l-img {
            background: #f00 no-repeat center;
            background-size: 80px;
            width: 80px;
            height: 80px;
            border-radius: 50%;
        }
        .l-s-img {
            background: #f00 no-repeat center;
            background-size: 80px;
            width: 60px;
            height: 60px;
            position: absolute;
            border-radius: 50%;
            left: 30px;
        }
        .r-text {
            margin-left: -40px;
            padding: 20px 0 20px 50px;
            flex: 1;
            border-radius: 0 10px 10px 0;
            background: #fff;
            box-shadow: 0 0 20px rgba(0, 0, 0, .09);
            margin-right: 10px;
            .play-btn {
                position: absolute;
                bottom: 20px;
                background: #f00;
                color: #fff;
                border-radius: 20px;
                padding: 2px 15px;
                right: 15px;
                box-shadow: 0 0 10px rgba(255, 0, 0, .3);
            }
        }
    }
}
.amimate-close {
    position: absolute;
    top: 10px;
    left: 10px;
    z-index: 50;
    a {
        display: inline-block;
        color: #fff;
        &:before {
            content: '';
            height: 10px;
            display: inline-block;
            vertical-align: baseline;
            width: 10px;
            border-top: 1px solid #fff;
            border-left: 1px solid #fff;
            transform: rotate(-45deg);
        }
    }
}
.amimate-view {
    height: 100%;
    width: 100%;
    .amimate-item {
        position: absolute;
        width: 100%;
        height: 100%;
        background: #000;
        overflow: hidden;
        .am-content {
            background: #fff;
            transform-origin: right;
            box-sizing: border-box;
            width: 100%;
            height: 100%;
        }
    }
}
</style>



