<template>
    <div class="ui-container">
        <div class="amimate-item">
            <div class="am-content mu-player">
                <div class="amimate-close" @click="toLister"><a>返回</a></div>
                <div class="play-main">
                    <div class="play-title">
                        <p class="na">{{list[index].songname}}</p>
                        <p class="de">{{list[index].singername}}</p>
                    </div>
                    <div class="play-cd">
                        <span class="x-img"><img :src="getImg(list[index].albummid)" class="image-cd" :class="status=='play'?'rotate':''"></span>
                    </div>
                    <section class="play-bottom">
                        <div class="play-progress">
                            <span class="s">{{formaterTime(currentTime)}}</span>
                            <div class="progress-bar"><i :style="`width: ${barWidth}%`"></i></div>
                            <span class="e">{{formaterTime(duration)}}</span>
                        </div>
                        <div class="play-control">
                            <span class="btn-prev" @click="onPrev">
                                <i class="icon iconfont icon-icon_bofangqi_zuo"></i>
                            </span>
                            <span class="btn-play" @click="onPlayPress">
                                <i v-if="status=='play'" class="icon iconfont icon-tushubofangqizanting"></i>
                                <i v-if="status=='stop'" class="icon iconfont icon-tushubofangqibofang"></i>
                            </span>
                            <span class="btn-next" @click="onNext">
                                <i class="icon iconfont icon-icon_bofangqi_you"></i>
                            </span>
                        </div>
                    </section>
                </div>
                <img :src="getImg(list[index].albummid)" class="ms">
            </div>
        </div>
    </div>
</template>
<script>
export default {
    created () {
        this.$store.commit('setIndex', this.$route.query.id)
    },
    computed: {
        status () {
            return this.$store.state.status
        },
        index () {
            return this.$store.state.index
        },
        barWidth () {
            return Number(this.$store.state.currentTime * 100 / this.$store.state.duration).toFixed()
        },
        list () {
            return this.$store.state.list
        },
        duration () {
            return this.$store.state.duration
        },
        currentTime () {
            return this.$store.state.currentTime
        }
    },
    methods: {
        onPrev () {
            let prevIndex = Number(this.index)
            if (Number(this.index) - 1 >= 0) {
                prevIndex = Number(this.index) - 1
            } else {
                prevIndex = this.list.length - 1
            }
            this.$store.commit('setIndex', prevIndex)
            this.$router.replace({path: '/player', query: {id: prevIndex}})
        },
        onNext () {
            let nextIndex = Number(this.index)
            if (Number(this.index) + 1 < this.list.length) {
                nextIndex = Number(this.index) + 1
            } else {
                nextIndex = 0
            }
            this.$store.commit('setIndex', nextIndex)
            this.$router.replace({path: '/player', query: {id: nextIndex}})
        },
        getImg (id) {
            return `https://y.gtimg.cn/music/photo_new/T002R300x300M000${id}.jpg`
        },
        formaterTime (time) {
            let s = Number(time / 60).toFixed(2)
            return s < 10 ? '0' + s : s
        },
        onPlayPress () {
            let audioEle = document.querySelector('#h5audio_media')
            if (this.status === 'play') {
                audioEle.pause()
                this.$store.commit('setStatus', 'stop')
            } else {
                audioEle.play()
                this.$store.commit('setStatus', 'play')
            }
        },
        toLister () {
            this.$router.go(-1)
        }
    }
}
</script>

<style lang="scss">
@import '../assets/style/global.scss';
.tabs {
    display: flex;
    a {
        flex: 1;
        text-align: center;
        line-height: 50px;
        &.on {
            color: #f00;
        }
    }
}
.mu-player {
    overflow: hidden;
    display: flex;
    img.ms {
        position: absolute;
        z-index: 0;
        top: -50%;
        left: -50%;
        z-index: 3;
        height: 200%;
        object-fit: cover;
        transform: scale(1.1) translateZ(0);
        filter: blur(36px);
    }
    .play-main {
        display: flex;
        width: 100%;
        height: 100%;
        flex-direction: column;
        position: relative;
        z-index: 10;
    }
    .play-cd {
        display: flex;
        flex: 1;
        padding: 0 20px;
        align-items: center;
        justify-content: center;
        .x-img {
            display: block;
            width: 100%;
            padding-top: 100%;
            position: relative;
        }
        img.image-cd {
            top: 0;
            position: absolute;
            display: block;
            width: 100%;
            height: auto;
            border-radius: 50%;
            box-sizing: border-box;
            border: 10px solid rgba(0, 0, 0, .2);
            z-index: 100;
            transition: 10s;
            &.rotate {
                animation: rotate 10s linear infinite;
            }
        }
    }
    .play-title {
        color: #fff;
        text-align: center;
        padding-top: 10px;
        .na {
            font-size: 18px;
            padding-bottom: 5px;
        }
    }
    .play-progress {
        padding: 0 20px 20px;
        display: flex;
        align-items: center;
        color: #fff;
        .s {
            width: 50px;
        }
        .e {
            width: 50px;
            text-align: right;
        }
        .progress-bar {
            flex: 1;
            height: 4px;
            background: rgba(0, 0, 0, .2);
            i {
                background: #f00;
                width: 0;
                display: block;
                height: 4px;
                position: relative;
                &:after {
                    content: '';
                    position: absolute;
                    background: #f00;
                    border-radius: 50%;
                    width: 16px;
                    height: 16px;
                    right: -8px;
                    top: -6px;
                }
            }
        }
    }
    .play-control {
        padding:0 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        span {
            display: block;
            i{
                color: #fff;
                font-size: 60px;
            }
        }
        .btn-play {
        }
    }
    .play-bottom {
        height: 130px;
    }
}
.music-list {
    padding-bottom: 20px;
    .music-item {
        margin-bottom: 20px;
        padding: 0 20px;
        display: flex;
        align-items: center;
        position: relative;
        .l-img {
            background: #f00 no-repeat center;
            background-size: 80px;
            width: 80px;
            height: 80px;
            border-radius: 50%;
        }
        .l-s-img {
            background: #f00 no-repeat center;
            background-size: 80px;
            width: 60px;
            height: 60px;
            position: absolute;
            border-radius: 50%;
            left: 30px;
        }
        .r-text {
            margin-left: -40px;
            padding: 20px 0 20px 50px;
            flex: 1;
            border-radius: 0 10px 10px 0;
            background: #fff;
            box-shadow: 0 0 20px rgba(0, 0, 0, .09);
            margin-right: 10px;
            .play-btn {
                position: absolute;
                bottom: 20px;
                background: #f00;
                color: #fff;
                border-radius: 20px;
                padding: 2px 15px;
                right: 15px;
                box-shadow: 0 0 10px rgba(255, 0, 0, .3);
            }
        }
    }
}
.amimate-close {
    position: absolute;
    top: 12px;
    left: 10px;
    z-index: 50;
    a {
        display: inline-block;
        color: #fff;
        &:before {
            content: '';
            height: 10px;
            display: inline-block;
            vertical-align: baseline;
            width: 10px;
            border-top: 1px solid #fff;
            border-left: 1px solid #fff;
            transform: rotate(-45deg);
        }
    }
}
.amimate-view {
    height: 100%;
    width: 100%;
    .amimate-item {
        position: absolute;
        width: 100%;
        height: 100%;
        background: #000;
        overflow: hidden;
        .am-content {
            background: #fff;
            transform-origin: right;
            box-sizing: border-box;
            width: 100%;
            height: 100%;
        }
    }
}

@keyframes rotate {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
</style>



