<template>
    <div id="app">
        <transition :name="viewTransition">
            <router-view/>
        </transition>
        <audio
            ref="mp3"
            id="h5audio_media"
            height="0"
            width="0"
            :src="uri"
            @loadeddata="onloadeddata"
            @timeupdate="ontimeupdate"
            @ended="onended"
            @load="onload"
            @playing="onplaying"
            @canplaythrough="oncanplaythrough">
        </audio>
    </div>
</template>
<script>
import store from '@/store'
export default {
    name: 'App',
    data () {
        return {uri: ''}
    },
    watch: {
        index (a) {
            store.commit('duration', 0)
            store.commit('currentTime', 0)
            this.$store.commit('setStatus', 'stop')
            this.getvkey(this.music.songmid).then(() => {
                this.$refs.mp3.play()
                this.$store.commit('setStatus', 'play')
            })
        }
    },
    computed: {
        index () {
            return store.state.index
        },
        list () {
            return store.state.list
        },
        music () {
            return store.state.list[store.state.index]
        },
        direction () {
            return store.state.direction
        },
        viewTransition () {
            if (!this.direction) return ''
            return this.direction
        }
    },
    methods: {
        getvkey (id) {
            return this.$jsonp(`${location.protocol}//c.y.qq.com/base/fcgi-bin/fcg_music_express_mobile3.fcg`, {
                cid: '205361747',
                format: 'json',
                callback: '3211',
                uin: '1055127249',
                songmid: id,
                filename: `C400${id}.m4a`,
                guid: '7202115510'
            }).then(json => {
                this.uri = `https://dl.stream.qqmusic.qq.com/C400${id}.m4a?vkey=${json.data.items[0].vkey}&guid=7202115510&uin=1055127249&fromtag=66`
            }).catch(err => {
                console.log(err)
            })
        },
        onloadeddata () {
            // alert('当加载媒介数据时运行脚本')
            store.commit('duration', this.$refs.mp3.duration)
        },
        // 当媒介改变其播放位置时运行脚本
        ontimeupdate () {
            store.commit('duration', this.$refs.mp3.duration)
            store.commit('currentTime', this.$refs.mp3.currentTime)
        },
        onended () {
            let nextIndex = Number(this.index)
            if (Number(this.index) + 1 < this.list.length) {
                nextIndex = Number(this.index) + 1
            } else {
                nextIndex = 0
            }
            this.$store.commit('setIndex', nextIndex)
        },
        // 当媒介数据已开始播放时运行脚本
        onplaying () {
            // alert('播放')
        },
        // 当浏览器正在取媒介数据时运行脚本
        onprogress () {
            // alert('啊啊啊')
        },
        oncanplaythrough () {
            // alert('sss')
        },
        onload () {
            // alert('kakl')
        }
    },
    mounted () {
        // var audio = this.$refs.mp3
        this.getvkey(this.music.songmid).then(() => {
            // audio.play()
        })
    }
}

</script>
<style lang="scss">
    html,body {
        height: 100%;
    }
    #app {
        height: 100%;
    }
</style>



